<template>
  <b-nav-item
    v-if="!isDropdown"
    :href="route.path"
    link-classes="text-secondary p-3 px-4"
  >
    {{ route.name }}
  </b-nav-item>
  <b-nav-item-dropdown
    v-else
    :text="route.name"
    toggle-class="text-secondary p-3 px-4"
    right
  >
    <b-dropdown-item
      v-for="(children, index) in route.children"
      :key="index"
      :href="`${route.path}/${children.path}`"
    >
      {{ children.name }}
    </b-dropdown-item>
  </b-nav-item-dropdown>
</template>

<script>
export default {
  props: {
    route: Object
  },
  data () {
    return {
      isDropdown: false
    }
  },
  mounted () {
    this.isDropdown = this.route.children && this.route.children.length > 0
  }
}
</script>
