<template>
  <div class="home d-flex flex-column h-100">
    <Top />
    <!-- Begin page content -->
    <main role="main" class="flex-shrink-0 mb-4">
      <b-carousel
        id="carousel-1"
        :interval="4000"
        controls
        indicators
        background="#ababab"
        img-width="1920"
        img-height="997"
        class="mb-3"
      >
        <!-- Slides with image only -->
        <b-carousel-slide img-src="../assets/banner.jpg"></b-carousel-slide>

        <!-- Slide with blank fluid image to maintain slide aspect ratio -->
        <b-carousel-slide caption="Blank Image" img-blank img-alt="Blank image" />>
      </b-carousel>

      <b-container class="position-relative">
        <SideCategory />

        <b-breadcrumb :items="breadcrumb"></b-breadcrumb>

        <b-row id="product-table"
          cols="2" cols-md="4">
          <ProductComp
            v-for="(product, index) in currentPageProducts"
            :key="index"
            :product="product"
          />
          </b-row>

          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            @change="setCurrentProduct"
            aria-controls="product-table"
            align="center"
          ></b-pagination>
      </b-container>
    </main>

    <StickyFooter />

    <!-- <font-awesome-icon icon="user-secret" />
    <img v-holder="'img=100px200?auto=yes&theme=social&text=Full width'" /> -->
    <!-- <img alt="Vue logo" src="../assets/banner.jpg" /> -->
  </div>
</template>

<script>
// @ is an alias to /src
import { mapState } from 'vuex'

import Top from '@/components/Top.vue'
import StickyFooter from '@/components/StickyFooter.vue'
import SideCategory from '@/components/SideCategory.vue'
import ProductComp from '@/components/ProductComp.vue'

//
export default {
  name: 'Home',
  components: {
    Top,
    StickyFooter,
    SideCategory,
    ProductComp
  },
  data () {
    return {
      breadcrumb: [
        {
          text: 'TODO::',
          href: '#'
        },
        {
          text: 'Category',
          href: '#'
        },
        {
          text: 'Filter',
          active: true
        }
      ],
      perPage: 12,
      currentPage: 1,
      currentPageProducts: []
    }
  },
  computed: {
    ...mapState([
      'products'
    ]),
    rows () {
      return this.products.length
    }
  },
  methods: {
    setCurrentProduct () {
      const startIdx = (this.currentPage - 1) * this.perPage
      const endIdx = startIdx + this.perPage
      this.currentPageProducts = this.products.slice(startIdx, endIdx)
    }
  },
  watch: {
    currentPage: {
      immediate: true,
      handler () {
        this.setCurrentProduct()
      }
    }
  },
  mounted () {
    this.setCurrentProduct()
  }
}
</script>
