<template>
  <div class="product d-flex flex-column h-100">
    <Top />
    <main role="main" class="flex-shrink-0 mb-4">
      <b-container class="position-relative">
        <SideCategory />

        <b-breadcrumb :items="breadcrumb"></b-breadcrumb>

        <b-card>
          <b-media no-body>
            <b-media-aside vertical-align="center">
              <b-img blank blank-color="#ccc" width="128" height="256" alt="placeholder"></b-img>
            </b-media-aside>

            <b-media-body class="ml-3">
              <h5 class="mt-0">{{ getProduct().name }}</h5>
              <p>
                Cras sit amet nibh libero, in gravida nulla. Nulla vel metus scelerisque ante
                sollicitudin. Cras purus odio, vestibulum in vulputate at, tempus viverra turpis. Fusce
                condimentum nunc ac nisi vulputate fringilla. Donec lacinia congue felis in faucibus.
              </p>
              <p class="mb-0">
                Donec sed odio dui. Nullam quis risus eget urna mollis ornare vel eu leo. Cum sociis
                natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus.
              </p>
            </b-media-body>
          </b-media>
        </b-card>

      </b-container>
    </main>
    <StickyFooter />
  </div>
</template>

<script>
import { mapState } from 'vuex'

import Top from '@/components/Top.vue'
import StickyFooter from '@/components/StickyFooter.vue'
import SideCategory from '@/components/SideCategory.vue'

export default {
  name: 'Product',
  components: {
    Top,
    StickyFooter,
    SideCategory
  },
  data () {
    return {
      breadcrumb: [
        {
          text: 'TODO::',
          href: '#'
        },
        {
          text: 'Category',
          href: '#'
        },
        {
          text: 'Filter',
          active: true
        }
      ]
    }
  },
  computed: {
    ...mapState([
      'products'
    ])
  },
  methods: {
    getProduct () {
      let product

      for (let index = 0; index < this.products.length; index++) {
        if (this.products[index].id === this.$route.params.id) {
          product = this.products[index]
          break
        }
      }
      return product
    }
  }
}
</script>
